import React, { FC, useState } from "react";
import { Viewer } from "./components/viewer";

const pdfs = ["test.pdf", "test2.pdf", "test3.pdf", "test4.pdf"];

const App: FC = () => {
  const [url, setUrl] = useState<string>("test2.pdf");
  console.log("url", url);
  return (
    <div className="App">
      <div
        style={{
          position: "fixed",
          bottom: 0,
          left: 0,
          zIndex: 9999,
        }}
      >
        <span>Select PDF from dropdown</span>
        <select
          value={url}
          onChange={(e) => {
            setUrl(e.target.value);
          }}
        >
          {pdfs.map((it) => (
            <option value={it}>{it}</option>
          ))}
        </select>
      </div>
      <Viewer
        url={url}
        options={{
          startingScale: 1.5,
        }}
      />
    </div>
  );
};

export default App;
