import { PDFEngine } from "./pdf-engine";
import { FontData } from "./types";

// represents an area inside on the pdf
export class PDFRect {
  // dimensions in PDF "user space"
  top: number;
  left: number;
  right: number;
  bottom: number;

  // position in device "user space"
  x: number | undefined;
  y: number | undefined;

  constructor(top: number, left: number, right: number, bottom: number) {
    this.top = top;
    this.left = left;
    this.right = right;
    this.bottom = bottom;
  }

  get width() {
    return Math.abs(this.left - this.right);
  }

  get height() {
    return Math.abs(this.top - this.bottom);
  }

  // returns true if given rect is "close" to current rect
  // TODO: you need to rework this!!
  isClose(rect: PDFRect, tolerance = 14.0) {
    return (
      Math.abs(this.right - rect.left) < 8.0 &&
      Math.abs(this.top - rect.top) < this.height &&
      Math.abs(this.height - rect.height) < 20.0
    );
  }

  // returns new rect with merged properties to contain both rects
  merge(rect: PDFRect) {
    const top = Math.max(rect.top, this.top);
    const bottom = Math.min(rect.bottom, this.bottom);
    const left = Math.min(rect.left, this.left);
    const right = Math.max(rect.right, this.right);
    const merged = new PDFRect(top, left, right, bottom);
    if (rect.x && rect.y && this.y) {
      merged.x = rect.x;
      merged.y = Math.min(rect.y, this.y);
    }
    return merged;
  }

  copy() {
    const copy = new PDFRect(this.top, this.left, this.right, this.bottom);
    copy.x = this.x;
    copy.y = this.y;
    return copy;
  }

  // returns and sets the coordinates expressed in the device system
  setDeviceOrigin(pagePtr: number, pageWidth: number, pageHeight: number) {
    const { x, y } = PDFEngine.getPDFCoords(pagePtr, pageWidth, pageHeight, {
      x: this.left,
      y: this.top,
    });
    this.x = x;
    this.y = y;
  }

  isEmpty() {
    return (
      this.x === undefined ||
      this.y === undefined ||
      this.width === 0 ||
      this.height === 0 ||
      this.width <= 1.0 ||
      this.top <= 1.0
    );
  }
}

export class FSMatrix {
  a: number;
  b: number;
  c: number;
  d: number;
  e: number;
  f: number;
  constructor(
    a: number,
    b: number,
    c: number,
    d: number,
    e: number,
    f: number
  ) {
    this.a = a;
    this.b = b;
    this.c = c;
    this.d = d;
    this.e = e;
    this.f = f;
  }

  isEqual(matrix: FSMatrix) {
    return (
      this.a === matrix.a &&
      this.b === matrix.b &&
      this.c === matrix.c &&
      this.d === matrix.d &&
      this.e === matrix.e &&
      this.f === matrix.f
    );
  }
}

export class PDFTextRect {
  fontData: FontData | undefined;
  text: string = "";
  rect: PDFRect;
  constructor(rect: PDFRect, fontData?: FontData) {
    this.fontData = fontData;
    this.rect = rect;
  }

  isSameFont(r: PDFTextRect) {
    if (!this.fontData) return true;
    const { fontFamily, fontSize, fsMatrix } = this.fontData;
    return (
      fontFamily === r.fontData?.fontFamily &&
      fontSize === r.fontData?.fontSize &&
      fsMatrix.isEqual(r.fontData?.fsMatrix)
    );
  }

  isEqual(r: PDFTextRect) {
    return this.isSameFont(r) && this.text === r.text;
  }

  // returns text inside bounding box of this rect
  getBoundedText(textPagePtr: number) {
    this.text = PDFEngine.getTextFromRect(textPagePtr, this.rect);
  }

  merge(rect: PDFTextRect): PDFTextRect | undefined {
    if (this.isEqual(rect)) {
      const merged = this.rect.merge(rect.rect);
      return new PDFTextRect(merged, this.fontData);
    }
  }
}
