import { useCallback, useState } from "react";

export const useCallbackRef = () => {
  const [target, setTarget] = useState<HTMLDivElement>();
  const ref = useCallback((el: HTMLDivElement) => {
    if (el != null) {
      setTarget(el);
    } else {
      setTarget(undefined);
    }
  }, []);
  return { ref, target };
};
