import { FC, ReactElement, useEffect, useRef, useState } from "react";
import { PDFPage } from "../core/pdf-page";
import { useIsPageVisible } from "../hooks/use-is-page-visible";
import { useViewer, useViewerScale } from "../hooks/use-viewer";
import { Loading } from "./loading";
import { useDebouncedRendering } from "../hooks/use-debounced-rendering";
import { SelectionLayer } from "./selection-layer";
import { PDFSelection } from "../core/pdf-selection";

type PageProps = {
  page: PDFPage;
  LoadingComponent?: ReactElement;
};

const showSelectionLayer = (index: number, isVisible: boolean) => {
  const isInSelection =
    PDFSelection.startPageIndex !== undefined &&
    PDFSelection.endPageIndex !== undefined &&
    index >= PDFSelection.startPageIndex &&
    index <= PDFSelection.endPageIndex;

  if (isInSelection) return true;
  if (!isInSelection && isVisible) return true;
  if (!isInSelection && !isVisible) return false;
};

export const Page: FC<PageProps> = ({ page }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const pageRef = useRef<HTMLDivElement>(null);
  const [loading, setLoading] = useState(true);
  const viewer = useViewer();
  const { scale } = useViewerScale();
  const isPageVisible = useIsPageVisible(pageRef);

  useDebouncedRendering(
    (newScale) => {
      if (isPageVisible && canvasRef.current)
        page.render(newScale, canvasRef.current, () => {
          setLoading(false);
        });
    },
    300,
    scale
  );

  useEffect(() => {
    if (isPageVisible) viewer.setPageIndex(page.index);
  }, [viewer, page, isPageVisible]);

  return (
    <div className="page-container-outer" data-page-index={page.index}>
      <div
        ref={pageRef}
        className="page-container"
        style={{
          width: Math.ceil(page.width * scale),
          height: Math.ceil(page.height * scale),
        }}
      >
        <canvas
          className="render-layer"
          style={{ background: "#fff" }}
          ref={canvasRef}
        />
        {isPageVisible && loading && <Loading />}
        {showSelectionLayer(page.index, isPageVisible) && (
          <SelectionLayer page={page} />
        )}
      </div>
    </div>
  );
};
