import { useContext, useEffect, useState } from "react";
import { ViewerContext } from "../components/viewer";
import {
  PDFViewer,
  PDFViewerSpecialZoom,
  PDFViewerState,
  PDFViewerStateListener,
} from "../core/pdf-viewer";

export const useViewer = () => useContext(ViewerContext);

export const useSubscribeViewer = (
  viewer: PDFViewer | undefined,
  onUpdate: (state: PDFViewerState) => void,
  selectors?: (keyof PDFViewerState)[]
) => {
  useEffect(() => {
    if (!viewer) return;
    const observer: PDFViewerStateListener = {
      update: onUpdate,
      selectors: selectors,
    };

    viewer.subscribe(observer);

    return () => {
      viewer.unsubscribe(observer);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [viewer]);
};

export const useViewerNavigation = () => {
  const viewer = useViewer();
  const { currentPage, pageCount } = viewer.getState();
  const [pageIndex, setPageIndex] = useState<number>(currentPage);
  const [pageNum, setPageNum] = useState<number>(pageCount);

  useSubscribeViewer(
    viewer,
    ({ currentPage, pageCount }) => {
      setPageIndex(currentPage);
      setPageNum(pageCount);
    },
    ["currentPage", "pageCount"]
  );

  return {
    pageIndex,
    pageCount: pageNum,
    goTo: (index: number) => viewer.goTo(index),
    prev: () => viewer.prev(),
    next: () => viewer.next(),
    isFirstPage: () => viewer.isFirstPage(),
    isLastPage: () => viewer.isLastPage(),
  };
};

export const useViewerScale = () => {
  const viewer = useViewer();
  const [currentScale, setCurrentScale] = useState(viewer.getState().scale);
  useSubscribeViewer(
    viewer,
    ({ scale }) => {
      setCurrentScale(scale);
    },
    ["scale"]
  );

  return {
    scale: currentScale,
    zoom: (z: number) => viewer.zoom(z),
    zoomIn: () => viewer.zoomIn(),
    zoomOut: () => viewer.zoomOut(),
    zoomToSpecialValue: (scale: PDFViewerSpecialZoom) =>
      viewer.zoomToSpecialValue(scale),
  };
};
