import { PDFEngine } from "./pdf-engine";
import { Coords } from "./pdf-page";
import { PDFCharBounds, PDFPageSelection } from "./types";

export class PDFSelection {
  static startPageIndex: number | undefined;
  static endPageIndex: number | undefined;
  static startCharIndex: number | undefined;
  static endCharIndex: number | undefined;
  static charBounds: PDFCharBounds[] = [];
  static _domRect: { left: number; top: number; width: number; height: number };
  static startPos: Coords | undefined;
  static endPos: Coords | undefined;

  static get isInvalid() {
    return (
      this.startPageIndex === undefined ||
      this.endPageIndex === undefined ||
      this.startCharIndex === undefined ||
      this.endCharIndex === undefined
    );
  }

  static get domRect() {
    return this._domRect;
  }

  static get isOnSamePage() {
    return (
      this.startPageIndex === this.endPageIndex &&
      this.startPageIndex !== undefined
    );
  }

  static get bounds(): PDFPageSelection[] {
    if (
      this.startPageIndex === undefined ||
      this.endPageIndex === undefined ||
      this.startCharIndex === undefined ||
      this.endCharIndex === undefined
    )
      return [];
    const bounds: PDFPageSelection[] = [];
    if (this.startPageIndex !== this.endPageIndex) {
      // get first and last bound
      const firstBound: PDFPageSelection = {
        index: this.startPageIndex,
        start: this.startCharIndex,
        end: -1,
      };
      const lastBound: PDFPageSelection = {
        index: this.endPageIndex,
        start: 0,
        end: this.endCharIndex,
      };
      bounds.push(firstBound);
      for (let i = this.startPageIndex + 1; i < this.endPageIndex; i++) {
        bounds.push({
          index: i,
          start: 0,
          end: -1,
        });
      }
      bounds.push(lastBound);
      return bounds;
    } else {
      return [
        {
          index: this.startPageIndex,
          start: this.startCharIndex,
          end: this.endCharIndex,
        },
      ];
    }
  }

  static start(textPagePtr: number, pageIndex: number, pos: Coords) {
    // clear previous end selection
    this.endCharIndex = undefined;
    this.endPageIndex = undefined;
    this.startPageIndex = pageIndex;
    this.startCharIndex = PDFEngine.getCharIndex(textPagePtr, pos);
    this.startPos = pos;
    if (this.startCharIndex < 0) {
      this.startPageIndex = undefined;
      this.startCharIndex = undefined;
      this.startPos = undefined;
    }
  }

  static stop(textPagePtr: number, pageIndex: number, pos: Coords) {
    this.endPageIndex = pageIndex;
    this.endCharIndex = PDFEngine.getCharIndex(textPagePtr, pos);
    this.endPos = pos;
    if (!this.startPos || !this.endPos) return;
    this._domRect = {
      left: Math.max(this.startPos.x, this.endPos.x),
      top: Math.max(this.startPos.y, this.endPos.y),
      width: Math.abs(this.startPos.x - this.endPos.x),
      height: Math.abs(this.startPos.y - this.endPos.y),
    };
  }
}
