import { FC, Fragment, useEffect, useRef, useState } from "react";
import { PDFPage } from "../core/pdf-page";
import { useViewerScale } from "../hooks/use-viewer";
import { PDFTextRect } from "../core/geometry";

type SelectionLayerProps = {
  page: PDFPage;
};

//* component to render a 'fake' selection so we don't have to display text
export const SelectionLayer: FC<SelectionLayerProps> = ({ page }) => {
  const selectionLayerRef = useRef<HTMLDivElement>(null);
  const [rects, setRects] = useState<PDFTextRect[]>(page.textRects);
  const { scale } = useViewerScale();

  useEffect(() => {
    setRects(page.getTextRects());
  }, [page]);

  return (
    <div
      ref={selectionLayerRef}
      data-layer-type="selection-layer"
      className="page-layer"
      onMouseDown={(e) => {
        if (!selectionLayerRef.current) return;
        const bounds = selectionLayerRef.current.getBoundingClientRect();
        page.startSelection({
          x: (e.clientX - bounds.left) / scale,
          y: (e.clientY - bounds.top) / scale,
        });
      }}
      onMouseUp={(e) => {
        if (!selectionLayerRef.current) return;
        const bounds = selectionLayerRef.current.getBoundingClientRect();
        page.stopSelection({
          x: (e.clientX - bounds.left) / scale,
          y: (e.clientY - bounds.top) / scale,
        });
      }}
    >
      {rects.map(({ rect, text }, index) => {
        if (rect.x === undefined || rect.y === undefined)
          return <Fragment key={index} />;

        const width = rect.width * scale;
        const height = rect.height * scale;
        const currentWidth = page.getTextLength(text);

        return (
          <span
            key={index}
            style={{
              display: "block",
              top: rect.y * scale,
              left: rect.x * scale,
              color: "transparent",
              fontSize: "16px!important",
              fontWeight: "400",
              transformOrigin: "0 0 0",
              fontFamily: "Arial, sans-serif",
              lineHeight: "1",
              margin: 0,
              position: "absolute",
              textAlign: "left",
              whiteSpace: "pre",
              opacity: 0.7,
              transform: `scale(${width / currentWidth}, ${height / 16})`,
            }}
          >
            {text}
          </span>
        );
      })}
    </div>
  );
};
