import { wrap, wrappedModuleMethods } from "./wrappers";
import { WrappedModule } from "pdfium/wrappers";
import { PdfiumModule } from "pdfium/pdfium";
import { CTypeName } from "pdfium/emscripten";
import { FSMatrix, PDFRect } from "./geometry";
import { Coords } from "./pdf-page";
import { PDFCharBounds } from "./types";

// should only be called once
export const createPDFEngineInstance = (pdfiumModule: PdfiumModule) => {
  PDFEngine.mod = wrap(pdfiumModule.cwrap, wrappedModuleMethods);
  PDFEngine.pdfiumModule = pdfiumModule;
  PDFEngine.init();
  PDFEngine.hasInitialized = true;
};

export class PDFEngine {
  static rectsDictionary: Record<string, number> = {};
  static hasInitialized: boolean;
  static mod: WrappedModule<typeof wrappedModuleMethods>;
  static pdfiumModule: PdfiumModule;
  static init() {
    // @ts-ignore
    this.mod.PDFium_Init();
  }
  static destroy() {
    // @ts-ignore
    this.mod.FPDF_DestroyLibrary();
  }
  static malloc(size: number) {
    const ptr = this.pdfiumModule._malloc(size);
    for (let i = 0; i < size; i++) {
      this.pdfiumModule.HEAP8[ptr + i] = 0;
    }
    return ptr;
  }
  static free(ptr: number) {
    this.pdfiumModule._free(ptr);
  }
  static setHEAPU8(array: ArrayLike<number>, offset?: number | undefined) {
    this.pdfiumModule.HEAPU8.set(array, offset);
  }
  static getBitmapArrayBuffer(ptr: number, len: number) {
    return new Uint8ClampedArray(
      this.pdfiumModule.HEAPU8.subarray(ptr, ptr + len)
    );
  }
  static getValue(ptr: number, value: CTypeName) {
    return this.pdfiumModule.getValue(ptr, value);
  }
  static UTF8ToString(ptr: number) {
    return this.pdfiumModule.UTF8ToString(ptr);
  }
  static UTF16ToString(ptr: number) {
    return this.pdfiumModule.UTF16ToString(ptr);
  }
  static stringToUTF8(
    str: string,
    outPtr: number,
    maxBytesToRead?: number | undefined
  ) {
    return this.pdfiumModule.stringToUTF8(str, outPtr, maxBytesToRead);
  }
  static readString(
    readChars: (buffer: number, bufferLength: number) => number,
    parseChars: (buffer: number) => string,
    defaultLength: number = 100
  ) {
    let buffer = this.malloc(defaultLength);
    for (let i = 0; i < defaultLength; i++) {
      this.pdfiumModule.HEAP8[buffer + i] = 0;
    }
    const actualLength = readChars(buffer, defaultLength);
    let str: string;
    if (actualLength > defaultLength) {
      this.free(buffer);
      buffer = this.malloc(actualLength);
      for (let i = 0; i < actualLength; i++) {
        this.pdfiumModule.HEAP8[buffer + i] = 0;
      }
      readChars(buffer, actualLength);
      str = parseChars(buffer);
    } else {
      str = parseChars(buffer);
    }
    this.free(buffer);
    return str;
  }
  static getTextFromRect(textPagePtr: number, rect: PDFRect) {
    const { left, top, right, bottom } = rect;
    const utf16Length = this.mod.FPDFText_GetBoundedText(
      textPagePtr,
      left,
      top,
      right,
      bottom,
      0,
      0
    );
    const bytesCount = (utf16Length + 1) * 2; // include NIL
    const textBuffer = this.malloc(bytesCount);
    this.mod.FPDFText_GetBoundedText(
      textPagePtr,
      left,
      top,
      right,
      bottom,
      textBuffer,
      utf16Length
    );
    const content = this.UTF16ToString(textBuffer);
    this.free(textBuffer);
    return content;
  }
  // returns the bounding box of a given character
  // identified by the textPage pointer and its index
  static getCharRect(textPagePtr: number, index: number): PDFRect {
    const topPtr = this.malloc(8);
    const leftPtr = this.malloc(8);
    const rightPtr = this.malloc(8);
    const bottomPtr = this.malloc(8);
    this.mod.FPDFText_GetCharBox(
      textPagePtr,
      index,
      leftPtr,
      rightPtr,
      bottomPtr,
      topPtr
    );

    const left = this.getValue(leftPtr, "double");
    const top = this.getValue(topPtr, "double");
    const right = this.getValue(rightPtr, "double");
    const bottom = this.getValue(bottomPtr, "double");

    this.free(leftPtr);
    this.free(topPtr);
    this.free(rightPtr);
    this.free(bottomPtr);
    return new PDFRect(top, left, right, bottom);
  }
  static getPDFCoords(
    pagePtr: number,
    pageWidth: number,
    pageHeight: number,
    coords: Coords
  ) {
    const deviceXPtr = this.malloc(4);
    const deviceYPtr = this.malloc(4);
    this.mod.FPDF_PageToDevice(
      pagePtr,
      0,
      0,
      pageWidth,
      pageHeight,
      0,
      coords.x,
      coords.y,
      deviceXPtr,
      deviceYPtr
    );
    const x = this.getValue(deviceXPtr, "i32");
    const y = this.getValue(deviceYPtr, "i32");

    this.free(deviceXPtr);
    this.free(deviceYPtr);

    return { x, y };
  }

  static getFontData(textPagePtr: number, charIndex: number) {
    const fontSize = PDFEngine.mod.FPDFText_GetFontSize(textPagePtr, charIndex);

    const fontNameLength = PDFEngine.mod.FPDFText_GetFontInfo(
      textPagePtr,
      charIndex,
      0,
      0,
      0
    );

    const bytesCount = fontNameLength + 1; // include NIL
    const textBufferPtr = this.malloc(bytesCount);
    const flagsPtr = this.malloc(4);
    this.mod.FPDFText_GetFontInfo(
      textPagePtr,
      charIndex,
      textBufferPtr,
      bytesCount,
      flagsPtr
    );
    const fontFamily = this.UTF8ToString(textBufferPtr);
    this.free(textBufferPtr);
    this.free(flagsPtr);

    // get matrix
    const matrixPtr = this.malloc(6 * 4);
    this.mod.FPDFText_GetMatrix(textPagePtr, charIndex, matrixPtr);
    const fsMatrix = this.createFSMatrix(matrixPtr);
    return { fontFamily, fontSize, fsMatrix };
  }
  static createFSMatrix(matrixPtr: number) {
    const a = this.getValue(matrixPtr, "float");
    const b = this.getValue(matrixPtr + 4, "float");
    const c = this.getValue(matrixPtr + 8, "float");
    const d = this.getValue(matrixPtr + 12, "float");
    const e = this.getValue(matrixPtr + 16, "float");
    const f = this.getValue(matrixPtr + 20, "float");
    return new FSMatrix(a, b, c, d, e, f);
  }
  static getRect(textPagePtr: number, index: number): PDFRect | undefined {
    const topPtr = this.malloc(8);
    const leftPtr = this.malloc(8);
    const rightPtr = this.malloc(8);
    const bottomPtr = this.malloc(8);
    const success = this.mod.FPDFText_GetRect(
      textPagePtr,
      index,
      leftPtr,
      topPtr,
      rightPtr,
      bottomPtr
    );
    if (!success) return;

    const left = this.getValue(leftPtr, "double");
    const top = this.getValue(topPtr, "double");
    const right = this.getValue(rightPtr, "double");
    const bottom = this.getValue(bottomPtr, "double");

    this.free(leftPtr);
    this.free(topPtr);
    this.free(rightPtr);
    this.free(bottomPtr);
    return new PDFRect(top, left, right, bottom);
  }
  static getCharIndex(textPagePtr: number, pos: Coords) {
    return this.mod.FPDFText_GetCharIndexAtPos(
      textPagePtr,
      pos.x,
      pos.y,
      10.0,
      10.0
    );
  }
  static getTextFromPage(textPagePtr: number, bounds: PDFCharBounds) {
    const bufferPtr = this.malloc((bounds.offset + 1) * 2);
    this.mod.FPDFText_GetText(
      textPagePtr,
      bounds.start,
      bounds.offset,
      bufferPtr
    );
    const text = this.UTF16ToString(bufferPtr);
    this.free(bufferPtr);
    return text;
  }
}
