import { PDFFile } from "./types";
import { PDFDocument } from "./pdf-document";
import { compareObjects } from "./utils";

export type PDFViewerState = {
  pageCount: number;
  currentPage: number;
  scale: number;
  renderingMode: PDFViewerRenderingMode;
};

export enum PDFViewerRenderingMode {
  Vertical = "vertical",
  SinglePage = "single-page",
  DualPage = "dual-page",
}

export enum PDFViewerSpecialZoom {
  PageFit = "page-fit",
  PageWidth = "page-width",
}

export type PDFViewerStateListener = {
  update: (state: PDFViewerState) => void;
  selectors?: (keyof PDFViewerState)[];
};

const DEFAULT_PDF_VIEWER_STATE: PDFViewerState = {
  pageCount: 0,
  currentPage: 0,
  scale: 1,
  renderingMode: PDFViewerRenderingMode.Vertical,
};

export type PDFViewerOptions = {
  startingScale?: number;
  startingRenderingMode?: PDFViewerRenderingMode;
};

export class PDFViewer {
  private state: PDFViewerState = DEFAULT_PDF_VIEWER_STATE;
  private listeners: PDFViewerStateListener[] = [];
  private document: PDFDocument | undefined;
  static ZOOM_UBOUND = 4.0;
  static ZOOM_LBOUND = 0.5;
  element: HTMLDivElement | null = null;

  constructor(options?: PDFViewerOptions) {
    this.setState({
      ...DEFAULT_PDF_VIEWER_STATE,
      ...(options && options.startingScale && { scale: options.startingScale }),
    });
  }

  setRenderingMode(renderingMode: PDFViewerRenderingMode) {
    this.setState({ renderingMode });
  }

  getPages() {
    if (!this.document) return [];
    return this.document.pages;
  }

  openDocument(file: PDFFile) {
    this.document = new PDFDocument();
    this.document.open(file);
    this.setState({ pageCount: this.document.pageCount });
    return this.document;
  }

  getDocument() {
    return this.document;
  }

  subscribe(listener: PDFViewerStateListener) {
    if (this.listeners.includes(listener)) return;
    this.listeners.push(listener);
  }

  unsubscribe(listener: PDFViewerStateListener) {
    const observerIndex = this.listeners.indexOf(listener);
    if (observerIndex === -1) return;
    this.listeners.splice(observerIndex, 1);
  }

  setPageCount(pageCount: number) {
    this.setState({ pageCount });
  }

  getState() {
    return this.state;
  }

  private notify(changedKeys: (keyof PDFViewerState)[]) {
    if (changedKeys.length === 0) return;
    this.listeners.forEach((observer) => {
      // send update only if selectors aren't provided or if some (or all) of the selectors have changed
      const sendUpdate =
        observer.selectors === undefined ||
        (observer.selectors &&
          observer.selectors.some((sel) =>
            changedKeys.some((key) => sel === key)
          ));

      if (sendUpdate) observer.update(this.state);
    });
  }

  setState(newState: Partial<PDFViewerState>) {
    const state = { ...this.state, ...newState };
    const changedKeys = compareObjects(this.state, state);
    this.state = state;
    this.notify(changedKeys);
  }

  // navigation
  //* updates page index in state
  setPageIndex(index: number) {
    // index is out of bounds
    if (index < 0 || index >= this.state.pageCount) {
      console.error(
        `Tried to go to index: ${index} when bounds are: [0, ${this.state.pageCount}]`
      );
      return;
    }
    this.setState({ currentPage: index });
  }

  findPagePosition(pageContainer: HTMLElement) {
    let top = 0;
    if (pageContainer.offsetParent) {
      do {
        top += pageContainer.offsetTop;
      } while ((pageContainer = pageContainer.offsetParent as HTMLElement));
      // account for toolbar height
      return top - 50;
    }
  }

  scrollToPage(index: number) {
    const pageContainer = document.querySelector(
      `div[data-page-index='${index}']`
    ) as HTMLElement;
    if (pageContainer && this.element) {
      this.element.scroll(0, this.findPagePosition(pageContainer) ?? 0);
    }
  }

  goTo(index: number) {
    this.setPageIndex(index);
    this.scrollToPage(index);
  }

  prev() {
    const newPageIndex = this.state.currentPage - 1;
    this.setPageIndex(newPageIndex);
    this.scrollToPage(newPageIndex);
  }

  next() {
    const newPageIndex = this.state.currentPage + 1;
    this.setPageIndex(newPageIndex);
    this.scrollToPage(newPageIndex);
  }

  // end navigation

  isFirstPage() {
    return this.state.currentPage === 0;
  }

  isLastPage() {
    return this.state.currentPage === this.state.pageCount - 1;
  }

  zoomIn() {
    this.zoom(this.state.scale + 0.1);
  }

  zoomOut() {
    this.zoom(this.state.scale - 0.1);
  }

  zoom(newScale: number) {
    const scale = Math.round(newScale * 100) / 100;
    this.setState({ scale: this.limitZoom(scale) });
  }

  zoomToSpecialValue(scale: PDFViewerSpecialZoom) {
    const pages = this.getPages();
    // no pages
    if (pages.length === 0) return;
    // get page size
    const { width, height } = pages[0];

    // no page DOM node
    if (!this.element) return;

    // get viewer size
    //* account for outer container padding
    const viewerWidth = this.element.clientWidth - 20;
    const viewerHeight = this.element.clientHeight;

    switch (scale) {
      case PDFViewerSpecialZoom.PageFit: {
        const newScale = viewerHeight / height;
        this.zoom(newScale);
        break;
      }
      case PDFViewerSpecialZoom.PageWidth: {
        const newScale = viewerWidth / width;
        this.zoom(newScale);
        break;
      }
    }
  }

  limitZoom(scale: number) {
    if (scale <= PDFViewer.ZOOM_LBOUND) return PDFViewer.ZOOM_LBOUND;
    if (scale >= PDFViewer.ZOOM_UBOUND) return PDFViewer.ZOOM_UBOUND;
    return scale;
  }

  // TODO: find better way to handle zooming with trackpad / mouse wheel
  variableZoomIn(delta: number) {
    const scale = Math.round(this.state.scale * delta * 100) / 100;
    this.setState({ scale: this.limitZoom(scale) });
  }
}
