import { PDFTextRect } from "./geometry";
import { BitmapFormat, RenderFlag } from "./types";
import { PDFEngine } from "./pdf-engine";
import { measureWidth } from "./utils";
import { PDFSelection } from "./pdf-selection";

export type PDFPageOptions = {
  width: number; // width of page
  height: number; // height of page
  index: number; // page index
  docPtr: number; // pointer to document in memory
};

// represents if page has been rendered and at what scale
type PageStatus = {
  rendered: boolean;
  scale: number;
};

export type PDFPageTextRect = {
  left: number;
  top: number;
  width: number;
  height: number;
  content?: string;
};

export type PDFViewerMouseCoordinates = {
  x: number;
  y: number;
};

export type Coords = {
  x: number;
  y: number;
};

export type PDFViewerSelection = {
  startPage?: number;
  endPage?: number;
  startIndex?: number;
  endIndex?: number;
};

export const DPR = window.devicePixelRatio || 1;

export class PDFPage {
  static format: BitmapFormat = BitmapFormat.Bitmap_BGRA;
  static bytesPerPixel = 4;
  private pageStatus: PageStatus | undefined;

  readonly docPtr: number;
  readonly width: number;
  readonly height: number;
  readonly index: number;
  textRects: PDFTextRect[] = [];

  constructor(opt: PDFPageOptions) {
    const { width, height, index, docPtr } = opt;
    this.width = width;
    this.height = height;
    this.index = index;
    this.docPtr = docPtr;
  }

  private shouldRender(scale: number) {
    return (
      !this.pageStatus ||
      this.pageStatus.scale !== scale ||
      !this.pageStatus.rendered
    );
  }

  // returns width & height of bitmap and its length in the heap
  private getBitmapSize(scale: number): { w: number; h: number; len: number } {
    const w = Math.ceil(this.width * scale * DPR);
    const h = Math.ceil(this.height * scale * DPR);
    const len = w * h * PDFPage.bytesPerPixel;
    return { w, h, len };
  }

  private renderPageAsBitmap(
    bitmapHeapPtr: number,
    bitmapWidth: number,
    bitmapHeight: number,
    bitmapHeapLen: number
  ) {
    const bitmapPtr = PDFEngine.mod.FPDFBitmap_CreateEx(
      bitmapWidth,
      bitmapHeight,
      PDFPage.format,
      bitmapHeapPtr,
      bitmapWidth * PDFPage.bytesPerPixel
    );
    PDFEngine.mod.FPDFBitmap_FillRect(
      bitmapPtr,
      0,
      0,
      bitmapWidth,
      bitmapHeight,
      0xffffffff
    );

    // do we need to this???
    const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.docPtr, this.index);

    PDFEngine.mod.FPDF_RenderPageBitmap(
      bitmapPtr,
      pagePtr,
      0,
      0,
      bitmapWidth,
      bitmapHeight,
      0, // rotation
      RenderFlag.REVERSE_BYTE_ORDER
    );
    const buffer = PDFEngine.getBitmapArrayBuffer(bitmapHeapPtr, bitmapHeapLen);

    PDFEngine.free(bitmapPtr);

    return { pagePtr, buffer };
  }

  // TODO: check performance on if we should 'cache' the results of `measureWidth`
  getTextLength(str: string) {
    if (PDFEngine.rectsDictionary[str]) {
      return PDFEngine.rectsDictionary[str];
    }
    const width = measureWidth(str);
    PDFEngine.rectsDictionary[str] = width;
    return width;
  }

  // return image data to use in a canvas
  render(scale: number, canvas: HTMLCanvasElement, onFinish: () => void) {
    // prevent render if we don't need to
    if (!this.shouldRender(scale)) return;
    this.pageStatus = { rendered: false, scale };

    const { w, h, len } = this.getBitmapSize(scale);
    // get pointer for bitmap
    const ptr = PDFEngine.malloc(len);
    // render page as bitmap and return HEAP buffer related to the given bitmap
    const { buffer, pagePtr } = this.renderPageAsBitmap(ptr, w, h, len);

    PDFEngine.mod.FPDFBitmap_Destroy(ptr);
    PDFEngine.mod.FPDF_ClosePage(pagePtr);
    PDFEngine.free(ptr);

    this.pageStatus = { rendered: true, scale };
    const imageData = new ImageData(buffer, w, h);
    canvas.width = imageData.width;
    canvas.height = imageData.height;
    const ctx = canvas.getContext("2d");
    if (!ctx) return;
    ctx.putImageData(imageData, 0, 0, 0, 0, imageData.width, imageData.height);
    onFinish();
  }

  //* this function returns the text bounds by manually merging chars
  // TODO: still need to iron a few issues out such as: double text ("bold simulation"), dotted lines, invisible text(?)
  //! this will probably be deprecated as it's hard to mantain
  getBoundsWithChars(): PDFTextRect[] {
    const now = performance.now();
    if (this.textRects.length > 0) {
      return this.textRects;
    }
    const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.docPtr, this.index);
    const textPagePtr = PDFEngine.mod.FPDFText_LoadPage(pagePtr);
    const charCount = PDFEngine.mod.FPDFText_CountChars(textPagePtr);

    let lastRect: PDFTextRect | undefined;

    const buffer: PDFTextRect[] = [];

    for (let i = 0; i < charCount; i++) {
      // get PDFRect which represents bounding box for the character at the given index
      const currentRect = PDFEngine.getCharRect(textPagePtr, i);
      // updates this PDFRect origin to match the device "user space"
      currentRect.setDeviceOrigin(pagePtr, this.width, this.height);
      const textRect = new PDFTextRect(currentRect);

      // !having this causes massive increase in load time of pages
      // !provide an option to be more precise at the cost of performance
      // * skip completely empty chars
      // const currentRectText = currentRect.getBoundedText(textPagePtr);
      // if (currentRectText.replace(/(\r\n|\n|\r)/gm, " ") === " ") continue;

      if (lastRect && lastRect.rect.isClose(currentRect)) {
        // TODO: make tolerance based on char font-size!! (actually should it be?)
        const merged = currentRect.merge(lastRect.rect);
        buffer[buffer.length - 1] = new PDFTextRect(merged);
        lastRect = buffer[buffer.length - 1];
      } else {
        lastRect = textRect;
        buffer.push(textRect);
      }

      // if we finished reading a word OR if we reached the end
      if (buffer.length > 1 || i === charCount - 1) {
        // only push to final text rects a non-empty rect
        if (!buffer[0].rect.isEmpty()) {
          buffer[0].getBoundedText(textPagePtr);
          buffer[0].text.replace(/(\r\n|\n|\r)/gm, "");
          this.textRects.push(buffer[0]);
        }
        buffer.shift();
      }
    }

    const end = performance.now();

    console.log(
      "Time to generate rects for this page ",
      (end - now) / 1000 + "s"
    );

    return this.textRects;
  }
  //* this is used for the selection layer
  getTextRects(start: number = 0, offset: number = -1): PDFTextRect[] {
    const now = performance.now();
    if (this.textRects.length > 0) {
      return this.textRects;
    }
    const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.docPtr, this.index);
    const textPagePtr = PDFEngine.mod.FPDFText_LoadPage(pagePtr);
    const rectsCount = PDFEngine.mod.FPDFText_CountRects(
      textPagePtr,
      start,
      offset
    );

    for (let i = 0; i < rectsCount; i++) {
      const currentRect = PDFEngine.getRect(textPagePtr, i);
      if (!currentRect) continue;
      // updates this PDFRect origin to match the device "user space"
      currentRect.setDeviceOrigin(pagePtr, this.width, this.height);
      const textRect = new PDFTextRect(currentRect);
      textRect.getBoundedText(textPagePtr);
      this.textRects.push(textRect);
    }
    const end = performance.now();
    console.log(
      "Time to generate rects for this page ",
      (end - now) / 1000 + "s"
    );
    return this.textRects;
  }

  getSelectedText(): string | undefined {
    if (PDFSelection.isInvalid) return;
    const pageSelection = PDFSelection.bounds.find(
      (b) => b.index === this.index
    );
    if (!pageSelection) return;

    const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.docPtr, this.index);
    const textPagePtr = PDFEngine.mod.FPDFText_LoadPage(pagePtr);
    let offset = pageSelection.end - pageSelection.start;
    if (pageSelection.end === -1) {
      offset = PDFEngine.mod.FPDFText_CountChars(textPagePtr);
    }
    return PDFEngine.getTextFromPage(textPagePtr, {
      start: pageSelection.start,
      offset,
    });
  }
  startSelection(coords: Coords) {
    const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.docPtr, this.index);
    const textPagePtr = PDFEngine.mod.FPDFText_LoadPage(pagePtr);
    const pdfCoords = PDFEngine.getPDFCoords(
      pagePtr,
      this.width,
      this.height,
      coords
    );
    PDFSelection.start(textPagePtr, this.index, pdfCoords);
  }
  stopSelection(coords: Coords) {
    const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.docPtr, this.index);
    const textPagePtr = PDFEngine.mod.FPDFText_LoadPage(pagePtr);
    const pdfCoords = PDFEngine.getPDFCoords(
      pagePtr,
      this.width,
      this.height,
      coords
    );
    PDFSelection.stop(textPagePtr, this.index, pdfCoords);
    console.log("getText", this.getSelectedText());
  }
}
