import { FC, useEffect, useState } from "react";
import { useViewerScale } from "../hooks/use-viewer";
import { ToolbarButton } from "./toolbar-button";
import { PDFViewerSpecialZoom } from "../core/pdf-viewer";

const Plus: FC = () => {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 448 512">
      <path d="M256 80c0-17.7-14.3-32-32-32s-32 14.3-32 32V224H48c-17.7 0-32 14.3-32 32s14.3 32 32 32H192V432c0 17.7 14.3 32 32 32s32-14.3 32-32V288H400c17.7 0 32-14.3 32-32s-14.3-32-32-32H256V80z" />
    </svg>
  );
};

const Minus: FC = () => {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 448 512">
      <path d="M432 256c0 17.7-14.3 32-32 32L48 288c-17.7 0-32-14.3-32-32s14.3-32 32-32l352 0c17.7 0 32 14.3 32 32z" />
    </svg>
  );
};

export const ZoomSection: FC = () => {
  const zoom = useViewerScale();
  const currentZoom = Math.round(zoom.scale * 100);
  const [selectedOption, setSelectedOption] = useState<
    number | PDFViewerSpecialZoom
  >(currentZoom);

  useEffect(() => {
    setSelectedOption(Math.round(zoom.scale * 100));
  }, [zoom]);

  const onChangeSelect = (value: string) => {
    switch (value) {
      case "page-width":
        zoom.zoomToSpecialValue(PDFViewerSpecialZoom.PageWidth);
        break;
      case "page-fit":
        zoom.zoomToSpecialValue(PDFViewerSpecialZoom.PageFit);
        break;
      default:
        zoom.zoom(parseInt(value) / 100);
    }
  };

  return (
    <div className="zoom">
      <ToolbarButton icon={<Plus />} onClick={() => zoom.zoomIn()} />
      <ToolbarButton icon={<Minus />} onClick={() => zoom.zoomOut()} />
      <select
        className="toolbar-select"
        value={selectedOption}
        onChange={(e) => {
          onChangeSelect(e.target.value);
        }}
      >
        <option hidden={true} value={currentZoom}>
          {currentZoom}%
        </option>
        <option value={50}>50%</option>
        <option value={100}>100%</option>
        <option value={150}>150%</option>
        <option value={200}>200%</option>
        <option value={PDFViewerSpecialZoom.PageWidth}>Page width</option>
        <option value={PDFViewerSpecialZoom.PageFit}>Page fit</option>
      </select>
    </div>
  );
};

type ToolbarProps = {
  pdfName?: string;
};

export const Toolbar: FC<ToolbarProps> = ({ pdfName }) => {
  return (
    <div className="toolbar-container">
      {pdfName && <h1>{pdfName}</h1>}
      <ZoomSection />
    </div>
  );
};
