import {
  PDFViewer,
  PDFViewerRenderingMode,
  PDFViewerSpecialZoom,
  PDFViewerState,
} from "../core/pdf-viewer";

export type ExternalHandlers = {
  state: PDFViewerState | undefined;
  renderingManager: {
    setRenderingMode: PDFViewer["setRenderingMode"] | undefined;
  };
  navigationManager: {
    goTo: PDFViewer["goTo"] | undefined;
    prev: PDFViewer["prev"] | undefined;
    next: PDFViewer["next"] | undefined;
    isFirstPage: PDFViewer["isFirstPage"] | undefined;
    isLastPage: PDFViewer["isLastPage"] | undefined;
  };
  zoomManager: {
    zoom: PDFViewer["zoom"] | undefined;
    zoomIn: PDFViewer["zoomIn"] | undefined;
    zoomOut: PDFViewer["zoomOut"] | undefined;
    zoomToSpecialValue: PDFViewer["zoomToSpecialValue"] | undefined;
  };
};

export const useExternalHandlers = (
  viewer: PDFViewer | undefined
): ExternalHandlers => {
  if (!viewer)
    return {
      state: undefined,
      renderingManager: {
        setRenderingMode: undefined,
      },
      navigationManager: {
        goTo: undefined,
        prev: undefined,
        next: undefined,
        isFirstPage: undefined,
        isLastPage: undefined,
      },
      zoomManager: {
        zoom: undefined,
        zoomIn: undefined,
        zoomOut: undefined,
        zoomToSpecialValue: undefined,
      },
    };
  return {
    state: viewer.getState(),
    renderingManager: {
      setRenderingMode: (r: PDFViewerRenderingMode) =>
        viewer.setRenderingMode(r),
    },
    navigationManager: {
      goTo: (index: number) => viewer.goTo(index),
      prev: () => viewer.prev(),
      next: () => viewer.next(),
      isFirstPage: () => viewer.isFirstPage(),
      isLastPage: () => viewer.isLastPage(),
    },
    zoomManager: {
      zoom: (scale: number) => viewer.zoom(scale),
      zoomIn: () => viewer.zoomIn(),
      zoomOut: () => viewer.zoomOut(),
      zoomToSpecialValue: (scale: PDFViewerSpecialZoom) =>
        viewer.zoomToSpecialValue(scale),
    },
  };
};
