import { FC, PropsWithChildren } from "react";
import { createPortal } from "react-dom";
import { useTextSelection } from "../hooks/use-text-selection";

const Portal: FC<PropsWithChildren> = ({ children }) => {
  return createPortal(children, document.body);
};

export const SelectionContextMenu: FC<{
  target: HTMLDivElement | undefined;
}> = ({ target }) => {
  const { isCollapsed, clientRect } = useTextSelection(target);
  if (clientRect == null || !target || isCollapsed) return null;

  return (
    <Portal>
      <div
        style={{
          position: "absolute",
          left: `${clientRect.left + clientRect.width / 2}px`,
          top: `${clientRect.top - 30}px`,
          transform: `translateX(-50%)`,
          background: "#29292B",
          padding: "2px 5px 2px 5px",
          border: "none",
          textAlign: "center",
          color: "white",
          borderRadius: "3px",
        }}
      >
        LOLOLOLOLOL
      </div>
    </Portal>
  );
};
