// compares two objects of the same type
// returns which keys have changed
export const compareObjects = <T extends Record<string, unknown>>(
  obj1: T,
  obj2: T
) => {
  const changedKeys: string[] = [];
  Object.entries(obj1).forEach(([key, value]) => {
    if (value !== obj2[key as keyof T]) changedKeys.push(key);
  });
  return changedKeys as (keyof T)[];
};

// TODO: see if possible to get the images from the bitmap array directly
// coverts an image data to a URL (yes I know it's kinda slow)
export const imageDataToDataUrl = (imageData: ImageData) => {
  const canvas = document.createElement("canvas");
  canvas.width = imageData.width;
  canvas.height = imageData.height;
  const context = canvas.getContext("2d");
  context?.putImageData(imageData, 0, 0);
  return canvas.toDataURL();
};

// TODO: check how performant this is
export const measureWidth = function (text: string) {
  // Create an element
  const ele = document.createElement("span");

  // Set styles
  ele.style.position = "absolute";
  ele.style.visibility = "hidden";
  ele.style.whiteSpace = "nowrap";
  ele.style.left = "-9999px";

  // Set font and text
  ele.style.fontFamily = "Arial, sans-serif";
  ele.style.fontSize = "16px!important";
  ele.style.fontWeight = "400";
  ele.innerText = text;

  // Append to the body
  document.body.appendChild(ele);
  const w = ele.getBoundingClientRect().width;
  document.body.removeChild(ele);
  return w;
};
