import {
  FC,
  ReactElement,
  createContext,
  useCallback,
  useEffect,
  useState,
} from "react";
import { PDFViewer, PDFViewerRenderingMode } from "../core/pdf-viewer";
import { Document } from "./pdf-document";
import { Toolbar } from "./toolbar";
import { PDFFile } from "../core/types";
import {
  ExternalHandlers,
  useExternalHandlers,
} from "../hooks/use-external-handlers";
import { createPDFEngineInstance } from "../core/pdf-engine";
import { createPDFiumModule } from "pdfium";
import { SelectionContextMenu } from "./selection-context-menu";
import { useCallbackRef } from "../hooks/use-callback-ref";
import { Navigator } from "./navigator";
import { usePinch } from "@use-gesture/react";

export type ViewerOptions = {
  startingScale?: number;
  startingRenderingMode?: PDFViewerRenderingMode;
  customPageLayers?: ReactElement[];
};

type ViewerProps = {
  url?: string;
  file?: PDFFile;
  options?: ViewerOptions;
  LoadingComponent?: ReactElement;
  showSelectionContextMenu?: boolean;
  setExternalHandlers?: (
    externalHandlers: ExternalHandlers
  ) => ExternalHandlers;
};

export const ViewerContext = createContext<PDFViewer>(new PDFViewer());

export const Viewer: FC<ViewerProps> = ({
  url,
  file,
  options,
  LoadingComponent,
  setExternalHandlers,
  showSelectionContextMenu = false,
}) => {
  const [viewer, setViewer] = useState<PDFViewer | undefined>();
  const [pdf, setPdf] = useState<PDFFile | undefined>(file);
  const [isEngineReady, setEngineReady] = useState(false);
  const externalHandlers = useExternalHandlers(viewer);
  const { ref, target } = useCallbackRef();
  const bind = usePinch((s) => {
    if (s.canceled) return;
    s.event.preventDefault();
    s.event.stopPropagation();
    // TODO: fix pinch & other gestures
    return;
  });

  useEffect(() => {
    if (!setExternalHandlers || !externalHandlers) return;
    setExternalHandlers(externalHandlers);
  }, [externalHandlers, setExternalHandlers]);

  useEffect(() => {
    // fetch the doc with url if no file was provided
    if (url !== undefined && !file) {
      const fetchDoc = async () => {
        const file = await fetch(url);
        const arrayBuffer = await file.arrayBuffer();
        setPdf({
          id: "1",
          name: "test.pdf",
          content: arrayBuffer,
        });
      };
      fetchDoc();
    }
  }, [file, url]);

  useEffect(() => {
    const initPdfium = async () => {
      const module = await createPDFiumModule({});
      createPDFEngineInstance(module);
      setEngineReady(true);
    };
    initPdfium();
  }, []);

  useEffect(() => {
    if (!pdf || !isEngineReady) return;
    const v = new PDFViewer({
      startingRenderingMode: options?.startingRenderingMode,
      startingScale: options?.startingScale,
    });
    setViewer(v);
    v.openDocument(pdf);
    return () => {
      setViewer(undefined);
    };
  }, [options, pdf, isEngineReady]);

  useEffect(() => {
    if (target && viewer) {
      viewer.element = target;
    }
  }, [viewer, target]);

  //* support for keyboard / touchpad zooming
  const handleScrollZoom = useCallback(
    (e: WheelEvent) => {
      // the property `buttons` define the active state
      // https://developer.mozilla.org/en-US/docs/Web/API/MouseEvent/buttons#value
      const unInitializedButton = e.buttons === 0;
      // only zoom in if alt / ctrl / meta key is pressed and NO mouse click is detected
      // else pan the view
      if (
        (e.altKey || e.ctrlKey || e.metaKey) &&
        unInitializedButton &&
        viewer
      ) {
        e.preventDefault();
        e.stopPropagation();
        // normalize deltaY with a MAX_ZOOM_STEP to not have big
        // jumps in the zoom when scrolling very fast
        const signY = Math.sign(e.deltaY);
        const absDeltaY = Math.abs(e.deltaY);
        let normalizedDeltaY = e.deltaY;
        const ZOOM_MAX_STEP = 10;
        if (absDeltaY > ZOOM_MAX_STEP) {
          normalizedDeltaY = ZOOM_MAX_STEP * signY;
        }
        const zoom = viewer.getState().scale;
        viewer.zoom(zoom * 0.99 ** normalizedDeltaY);
      }
    },
    [viewer]
  );

  useEffect(() => {
    if (target && viewer) {
      viewer.element = target;
    }
  }, [target, viewer]);

  useEffect(() => {
    if (!target) return;
    target.addEventListener("wheel", handleScrollZoom);
    return () => {
      target.removeEventListener("wheel", handleScrollZoom);
    };
  }, [target, handleScrollZoom]);

  return (
    <>
      {pdf && viewer && (
        <ViewerContext.Provider value={viewer}>
          <div className="pdf-viewer-layout">
            <div className="pdf-viewer-container">
              <Toolbar />
              <div ref={ref} className="pdf-viewer" {...bind()}>
                {showSelectionContextMenu && (
                  <SelectionContextMenu target={target} />
                )}
                <Document LoadingComponent={LoadingComponent} />
                <Navigator />
              </div>
            </div>
          </div>
        </ViewerContext.Provider>
      )}
    </>
  );
};
