import { FC } from "react";
import { ToolbarButton } from "./toolbar-button";
import { useViewerNavigation } from "../hooks/use-viewer";

const AngleLeft: FC = () => {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 320 512">
      <path d="M41.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l160 160c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L109.3 256 246.6 118.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-160 160z" />
    </svg>
  );
};

const AngleRight: FC = () => {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 320 512">
      <path d="M278.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-160 160c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L210.7 256 73.4 118.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l160 160z" />
    </svg>
  );
};

export const Navigator: FC = () => {
  const nav = useViewerNavigation();
  return (
    <div className="pdf-navigator">
      <ToolbarButton
        icon={<AngleLeft />}
        onClick={() => nav.prev()}
        disabled={nav.isFirstPage()}
      />
      <div className="pdf-navigator-counter">
        <span>
          {nav.pageIndex + 1} of {nav.pageCount}
        </span>
      </div>
      <ToolbarButton
        icon={<AngleRight />}
        onClick={() => nav.next()}
        disabled={nav.isLastPage()}
      />
    </div>
  );
};
