import { PDFPage } from "./pdf-page";
import { PDFFile } from "./types";
import { PDFEngine } from "./pdf-engine";

export class PDFDocument {
  pages: PDFPage[] = [];
  pageCount: number = 0;
  id: string | undefined;
  name: string | undefined;
  ptr: number | undefined;

  // eslint-disable-next-line @typescript-eslint/no-useless-constructor
  constructor() {}

  open(file: PDFFile, password: string = "") {
    this.id = file.id;
    this.name = file.name;
    const array = new Uint8Array(file.content);
    const length = array.length;
    const filePtr = PDFEngine.malloc(length);

    PDFEngine.setHEAPU8(array, filePtr);
    const passwordBytesSize = new TextEncoder().encode(password).length + 1;
    const passwordPtr = PDFEngine.malloc(passwordBytesSize);
    PDFEngine.stringToUTF8(password, passwordPtr, passwordBytesSize);

    this.ptr = PDFEngine.mod.FPDF_LoadMemDocument(filePtr, length, passwordPtr);

    PDFEngine.free(passwordPtr);

    if (!this.ptr) {
      // TODO: why ts error?
      // @ts-ignore
      const lastError = PDFEngine.mod.FPDF_GetLastError();
      PDFEngine.free(filePtr);
      console.error(lastError);
      return;
    }

    this.pageCount = PDFEngine.mod.FPDF_GetPageCount(this.ptr);
    const sizePtr = PDFEngine.malloc(8);

    for (let index = 0; index < this.pageCount; index++) {
      const result = PDFEngine.mod.FPDF_GetPageSizeByIndexF(
        this.ptr,
        index,
        sizePtr
      );
      if (result === 0) {
        // TODO: why ts error?
        // @ts-ignore
        const lastError = PDFEngine.mod.FPDF_GetLastError();
        PDFEngine.free(sizePtr);
        PDFEngine.mod.FPDF_CloseDocument(this.ptr);
        PDFEngine.free(passwordPtr);
        PDFEngine.free(filePtr);
        console.error(lastError);
        return;
      }

      const page = new PDFPage({
        height: PDFEngine.getValue(sizePtr + 4, "float"),
        width: PDFEngine.getValue(sizePtr, "float"),
        docPtr: this.ptr,
        index,
      });

      this.pages.push(page);
    }
    PDFEngine.free(sizePtr);
  }

  getMetadata() {
    if (this.ptr === undefined) {
      console.error("getMetadata: document does not exist");
      return;
    }
    return {
      title: this.readMetaText(this.ptr, "Title"),
      author: this.readMetaText(this.ptr, "Author"),
      subject: this.readMetaText(this.ptr, "Subject"),
      keywords: this.readMetaText(this.ptr, "Keywords"),
      producer: this.readMetaText(this.ptr, "Producer"),
      creator: this.readMetaText(this.ptr, "Creator"),
      creationDate: this.readMetaText(this.ptr, "CreationDate"),
      modificationDate: this.readMetaText(this.ptr, "ModDate"),
    };
  }

  readMetaText(docPtr: number, key: string) {
    return PDFEngine.readString(
      (buffer, bufferLength) =>
        PDFEngine.mod.FPDF_GetMetaText(docPtr, key, buffer, bufferLength),
      PDFEngine.UTF16ToString
    );
  }

  extractText(pageIndexes?: number[]) {
    if (this.ptr === undefined) return;
    let indexes = pageIndexes ?? [];
    if (!pageIndexes) {
      indexes = Array.from(Array(this.pageCount).keys());
    }

    const strings: string[] = [];
    for (let i = 0; i < indexes.length; i++) {
      const pagePtr = PDFEngine.mod.FPDF_LoadPage(this.ptr, indexes[i]);
      const textPagePtr = PDFEngine.mod.FPDFText_LoadPage(pagePtr);
      const charCount = PDFEngine.mod.FPDFText_CountChars(textPagePtr);
      const bufferPtr = PDFEngine.malloc((charCount + 1) * 2);
      PDFEngine.mod.FPDFText_GetText(textPagePtr, 0, charCount, bufferPtr);
      const text = PDFEngine.UTF16ToString(bufferPtr);
      PDFEngine.free(bufferPtr);
      strings.push(text);
    }
    return strings.join("\n\n");
  }
}
