import { useCallback, useEffect, useRef, useState } from "react";

// debounces rendering of pages by the given time to prevent
// calling the render function too quickly when changing scale
export const useDebouncedRendering = (
  renderPage: (scale: number) => void,
  time: number,
  scale: number
) => {
  const [debouncedScale, setDebouncedScale] = useState(scale);
  const mountedRef = useRef(false);
  const timeoutRef = useRef<NodeJS.Timer>();
  const cancel = useCallback(() => clearTimeout(timeoutRef.current), []);

  useEffect(() => {
    if (mountedRef.current) {
      cancel();
      timeoutRef.current = setTimeout(() => {
        setDebouncedScale(scale);
      }, time);
    }
  }, [scale, time, cancel]);

  useEffect(() => {
    mountedRef.current = true;
    return cancel;
  }, [cancel]);

  useEffect(() => {
    renderPage(debouncedScale);
  }, [renderPage, debouncedScale]);
};
